import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const StyledProjectLink = styled.span`
  padding: 0 0;
  font-weight: 600;
  font-size: 20px;
  letter-spacing: 1px;
  color: #0057a8;
  display: inline-block;

  a {
    text-decoration: none;
    color: #0057a8;
  }
`;

export default function ProjectLink({ item, linkSrc }) {
  return (
    <StyledProjectLink>
      {linkSrc ? (
        <a href={linkSrc} target="_blank" rel="noreferrer">
          {item}
        </a>
      ) : (
        item
      )}
    </StyledProjectLink>
  );
}

ProjectLink.propTypes = {
  item: PropTypes.object.isRequired,
  linkSrc: PropTypes.string.isRequired,
};
